<template>
<br>
<div class="top" style="text-align:center">
  <div style="text-align:left">
    <span class="welcomeMsg"><b>さぁ、すてきなストリーマーを探しに行きましょう。<br>あなたのお気に入りがきっと見つかる。</b></span>
  </div>
  <br>
  <br>
  <h1><b>視聴モード</b></h1>
  <div class="row">
    <div class="col-6" style="text-align:right">
      <router-link to="#" v-show="this.$root.mode == 0" v-on:click.prevent.stop="pushMode(0)">
        <img class="modeBtn" :src="this.$root.IMG_PATH + 'btn_mode1_on.png'">
      </router-link>
      <router-link to="#" v-show="this.$root.mode == 1" v-on:click.prevent.stop="pushMode(0)">
        <img class="modeBtn" :src="this.$root.IMG_PATH + 'btn_mode1_off.png'">
      </router-link>
    </div>
    <div class="col-6" style="text-align:left">
      <router-link to="#" v-show="this.$root.mode == 1" v-on:click.prevent.stop="pushMode(1)">
        <img class="modeBtn" :src="this.$root.IMG_PATH + 'btn_mode2_on.png'">
      </router-link>
      <router-link to="#" v-show="this.$root.mode == 0" v-on:click.prevent.stop="pushMode(1)">
        <img class="modeBtn" :src="this.$root.IMG_PATH + 'btn_mode2_off.png'">
      </router-link>
    </div>
  </div>
  <br>
  <hr style="height: 2px; background-color:white; border:none;">
  <h1><b>{{ searchLabel }}</b></h1>
  <br>
  <div class="search_box" style="width:80%">
    <input type="text" :value="searchWord" @input="updateList" class="form-control" placeholder="ゲーム名（英語表記）で検索" aria-label="Game Title" aria-describedby="search_button">
  </div>
  <div style="display:flex; flex-wrap:wrap; justify-content:center; margin:auto">
    <game-title-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></game-title-row>
  </div>
  <!-- <button v-show="this.isNext == true" class="cusButton" type="button" v-on:click="pushNext">
    <img :src="this.$root.IMG_PATH + 'btn_showmore.png'" style="width:150px">
  </button> -->

  <router-link v-show="this.isNext == true" to="#" v-on:click.prevent.stop="pushNext" style="width:300px;display:inline-block;margin-top:10px;padding:10px"><span class="showMore">Show more</span></router-link>
  <br>
  <br>
  <transition name="fade">
    <button type="button" class="cusButton" style="position:fixed; bottom:15px; right:15px" v-if="show" @click="moveTop">
      <img :src="this.$root.IMG_PATH + 'btn_pageUp.png'" style="height:50px">
    </button>
  </transition>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Top',
  components: {
  },
  data() {
    return {
      langIndex : 0,
      btnOn : "btn-primary",
      btnOff : "btn-secondary",
      pageIndex : 1,
      searchWord : "",
      isNext : false,
      searchLabel : "Top Games",

      dispDataList: [],
      filterDataList : [],
      allRowDataList: [],

      show : false,
    };
  },
  mounted () {
    window.addEventListener('scroll', () => {
        this.show = (window.scrollY > 150);

    });
  },
  methods : {
    updateList : function (e) {
      this.searchWord = e.target.value;
      if (this.searchWord == "") {
        //検索ワードなし
        this.filterDataList = this.allRowDataList;
        this.searchLabel = "Top Games (" + this.filterDataList.length + ")";
      } else {
        //検索ワードあり
        this.filterDataList = [];
        for (let i = 0; i < this.allRowDataList.length; i++) {
          const gameData = this.allRowDataList[i];
          if (gameData["name"].toUpperCase().indexOf(this.searchWord.toUpperCase()) > -1) {
            //一致
            this.filterDataList.push(gameData);
          }
        }
        this.searchLabel = "Search '" + this.searchWord + "' (" + this.filterDataList.length + ")";
      }
      console.log(this.searchLabel)
      this.pageIndex = 1;
      this.dispList();
    },
    dispList : function () {
      let max = 40 * this.pageIndex;
      if (max > this.filterDataList.length) {
        max = this.filterDataList.length;
        this.isNext = false;
      } else {
        this.isNext = true;
      }

      this.dispDataList = [];
      for (let i = 0; i < max; i++) {
        this.dispDataList.push(this.filterDataList[i]);          
      }
    },
    pushNext : function () {
      this.pageIndex = this.pageIndex + 1;
      this.dispList();
    },
    moveTop : function () {
      window.scrollTo(0, 0);   // スクロール位置を移動
    },
    pushMode : function (val) {
      this.$root.mode = val;
      localStorage.mode = val;
    },
    pushLang : function (index) {
      this.langIndex = index;
      console.log(index);
    },
    getGameList : function () {
      document.getElementById('collapse1').classList.remove('show');
      document.getElementById('collapse2').classList.remove('show');

      axios.get(this.$root.SERVER_PATH + 'getGames')
      .then((response) => {
        let resData = response.data;
        console.log(resData);

        this.allRowDataList = [];
        this.allRowDataList = resData["retList"];
        this.filterDataList = this.allRowDataList;
        this.searchLabel = "Top Games (" + this.filterDataList.length + ")";
        this.pageIndex = 1;

        let count = 0;
        for (let index = 0; index < this.allRowDataList.length; index++) {
          count += this.allRowDataList[index]["order"];
        }
        console.log(count + "ハイシ");

        this.dispList();

      }).catch((error) => {
        console.log(error.response);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.pageName = "Top";
      vm.getGameList();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.pageName = "Top";
      vm.getGameList();
    });
  },
}
</script>
