import { createApp } from 'vue/dist/vue.esm-bundler';
import App from './App.vue'
import router from './router'
import axios from 'axios'

const gameTitleRow = {
    props:["rowData"],
    template : `
	<div class="gameIcon">
        <div class="card" style="width:99%; height:95%; background-color:#ffffff00">
			<router-link :to="'/view/' + this.rowData.id" :title="this.rowData.name">
				<img :src="iconUrl" class="card-img-top" :alt="this.rowData.name">
				<div class="card-body" style="text-align:left; padding-left:0px; padding-top:5px">
					<h6 class="card-title title" :title="this.rowData.name"><b>{{ this.rowData.name }}</b></h6>
					<h6 class="card-title">配信数：{{ this.rowData.order }}</h6>
					<div v-show="this.rowData.view != 0"  style="display:flex;">
						<span class="viewCount"><img :src="this.$root.IMG_PATH + 'icon_view.png'" style="width:1.2rem;">&nbsp;{{ this.rowData.view }}人視聴中</span>
					</div>
					<div v-show="this.rowData.view == 0">
						&nbsp;
					</div>
				</div>
			</router-link>
        </div>
	</div>`,
	data: function () {
			return {
			}
	},
	computed : {
		iconUrl : function () {
            let iconUrl = this.rowData.icon;
            iconUrl = iconUrl.replace('{width}', '200');
            iconUrl = iconUrl.replace('{height}', '266');
			return iconUrl;
		},
	},
	methods : {
		pushGame (gameId) {
			console.log(gameId);
			this.$router.push('/view/' + gameId);
		}
	}
}


createApp(App)
.component("game-title-row", gameTitleRow)
.use(router, axios)
.mount('#app')
