<script>
  export default {
    data() {
      return {
        userId : "",
        mode : 0,

        IMG_PATH : "./app/img/",

        SERVER_PATH : "http://localhost:8001/api/",
        ROOT_PATH : "./",
        DOMAIN_PATH : "localhost",

        // SERVER_PATH : "https://iphone-taro.sakura.ne.jp/OneTwi/api/",
        // ROOT_PATH : "https://iphone-taro.sakura.ne.jp/OneTwi/",
        // DOMAIN_PATH : "iphone-taro.sakura.ne.jp",

        pageName : "",
      }
    },
    mounted () {
      if (localStorage.user) {
        this.userId = localStorage.user;
      } else {
        //ユーザーIDを作成
        this.userId = "";
        let randomStr = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
        for (let i = 0; i < 20; i++) {
          let ran = Math.random() * randomStr.length;
          this.userId = this.userId + randomStr.substr(ran, 1);
        }
        localStorage.user = this.userId;
      }
      console.log(this.userId);

      if (localStorage.mode) {
        this.mode = localStorage.mode;
        if (this.mode != 0 && this.mode != 1) {
          this.mode = 0;
          localStorage.mode = 0;
        }
      } else {
        this.mode = 0;
        localStorage.mode = 0;
      }
    },
    methods : {
      pushHome : function () {
        if (this.$root.pageName == "Top") {
          location.reload();
        } else {
          this.$router.push("/");
        }
      }
    }
  }
</script>



